import {ChangeEvent, useState} from "react";
import {Checkbox, FormControlLabel, Radio, RadioGroup} from "@mui/material";
import {Modal} from "../Modal";
import './style.scss';
import {ToggleItem} from "../ToggleItem";

type Props = {
    setOpenModal: (open: boolean) => void;
    setOpenSuccessModal: (open: boolean) => void;
}

export default function FormModal(props: Props) {
    const {setOpenModal, setOpenSuccessModal} = props;
    const resetValue = {
        companyName: '',
        numberOfEmployees: '',
        name: '',
        phone: '',
        email: '',
        education: '()',
        inquiries: '',
        terms01: false,
        terms02: false,
    };
    const [form, setForm] = useState(resetValue);

    const isButtonDisabled =
        !form.companyName ||
        !form.numberOfEmployees ||
        !form.name ||
        !form.phone ||
        !form.email ||
        !form.education ||
        !form.inquiries ||
        !form.terms01;

    const onChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        e?.stopPropagation();

        let formValue = {};
        const { name, value } = e?.target;
        const isTerms = name === 'terms01' || name === 'terms02';

        if ('checked' in e?.target && isTerms) {
            const { checked } = e?.target;
            formValue = {[name]: checked};
        } else {
            formValue = {[name]: value};
        }

        setForm((prevState) => ({
            ...prevState,
            ...formValue,
        }));
    }

    const onSubmit = () => {
        console.log(form);
        setOpenModal(false);
        setOpenSuccessModal(true);
    }

    return (
        <Modal setOpenModal={setOpenModal}>
            <form onSubmit={(e) => e?.preventDefault()}>
                <div className='modalHeader'>
                    <h2>  </h2>
                    <p>      .</p>
                </div>

                <div className='modalBody'>
                    <div className='modalBodyCont'>
                        <p className='require right'>*</p>

                        <table>
                            <colgroup>
                                <col width='15%'/>
                                <col width='85%'/>
                            </colgroup>
                            <tbody>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <input type='text' name='companyName' autoFocus onChange={onChange} />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <input type='text' name='numberOfEmployees' onChange={onChange} />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <input type='text' name='name' onChange={onChange} />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <input type='text' name='phone' onChange={onChange} />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <input type='text' name='email' onChange={onChange} />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <RadioGroup
                                        className='radioGroup'
                                        aria-labelledby="demo-radio-buttons-group-label"
                                        defaultValue="()"
                                        name="education"
                                        onChange={onChange}
                                    >
                                        <FormControlLabel value="()" control={<Radio/>}
                                                          label="()"/>
                                        <FormControlLabel value="( )" control={<Radio/>}
                                                          label="( )"/>
                                        <FormControlLabel value="" control={<Radio/>} label=""/>
                                    </RadioGroup>
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    
                                    <span> *</span>
                                </th>
                                <td>
                                    <textarea name='inquiries' maxLength={150} onChange={onChange} />
                                </td>
                            </tr>
                            </tbody>
                        </table>

                        <div className='terms'>
                            <ToggleItem
                                clickElement={
                                    <FormControlLabel
                                        control={<Checkbox name='terms01' onChange={onChange} />}
                                        label={<>     <span className='red'>()</span></>} />
                                }
                                toggleElement={
                                    <div>
                                        <h5>     () </h5>
                                        <p>
                                               ,   ,   .<br />
                                                .
                                        </p>
                                        <p>
                                            (1)    :       <br />
                                            (2)   : , , , , , <br />
                                            (3)   :     12
                                        </p>
                                    </div>
                                }
                            />

                            <ToggleItem
                                clickElement={
                                    <FormControlLabel
                                        control={<Checkbox name='terms02' onChange={(e) => {}} />}
                                        label={<>   <span className='red'>()</span></>} />
                                }
                                toggleElement={
                                    <div>
                                        <h5>   () </h5>
                                        <p>
                                                    ,<br />
                                               ,     .
                                        </p>
                                        <p>
                                            (1)    :     <br />
                                            (2)   : , , , <br />
                                            (3)   :   12
                                        </p>
                                    </div>
                                }
                            />
                        </div>
                    </div>
                </div>

                <div className='modalFooter'>
                    <button type='submit' disabled={isButtonDisabled} onClick={onSubmit}></button>
                </div>
            </form>
        </Modal>
    )
}
